export default {
  data() {
    return {
      selectLink: 'home',
      sliderPos: null,
      sliderWidth: null
    }
  },

  methods: {
    sliderLink() {
      let link= this.$refs[this.selectLink];
      var pos= link.offsetLeft;
      var width= link.offsetWidth;
      this.sliderPos= pos;
      this.sliderWidth= width;
    },

    link(link) {
      this.selectLink= link;
      this.sliderLink()
    },
  },

  mounted() {
    this.sliderLink()
  },
}
